<template>
  <div class="contact">
    <div class="contact-left">
      <div class="contact-tab">客户列表</div>
      <div class="contacts-search-box">
        <input v-model="search" type="text" placeholder="请输入电话号码或昵称">
        <span class="btn" @click="queryContacts">搜索</span>
      </div>
      <div class="contact-list">
        <div
          v-for="(user, key) in customers || []"
          :key="key"
          :class="selectedCustomer && selectedCustomer.openId === user.openId ?'user-item checked' : 'user-item'"
          @click="showCustomer(user)"
        >
          {{selectedCustomer && selectedCustomer.id}}{{user && user.id}}
          <div class="user-avatar">
            <img v-if="user.avatar" :src="user.avatar"/>
            <img v-else src="../images/default.png"/>
          </div>
          <div class="user-info" v-if="user.mark">{{ user.mark }}</div>
          <div class="user-info" v-else>{{ user.nickname }}</div>
        </div>
      </div>
    </div>
    <div class="contact-main">
      <div class="profile-card" v-if="selectedCustomer">
        <div class="card-title">
          <div class="profile-name">
            <i class="iconfont icon-zhanghu"></i>
            <div>{{ selectedCustomer.nickname }}</div>
          </div>
          <div class="profile-avatar">
            <img v-if="selectedCustomer.avatar" :src="selectedCustomer.avatar"/>
            <img v-else src="../images/default.png"/>
          </div>
        </div>
        <div class="info-item">
          <div class="info-name">备注</div>
          <div class="info-text">
            <div class="info-text-content" v-if="!markEditor.visible">{{ selectedCustomer.mark }}</div>
            <textarea class="info-text-input" v-else v-model="markEditor.content"></textarea>
          </div>
          <div class="info-edit">
            <img v-if="!markEditor.visible" @click="showMarkEditor" src="../images/edit.png" />
            <img v-else @click="saveMark" src="../images/finish.png" />
          </div>
        </div>
        <div class="info-item">
          <div class="info-name">应用名称</div>
          <div class="info-text">
            {{ selectedCustomer.appName }}
          </div>
        </div>
        <div class="info-item">
          <div class="info-name">手 机</div>
          <div class="info-text">
            <div class="info-text-content" v-if="!mobileEditor.visible">{{ selectedCustomer.mobileNumber }}</div>
            <textarea class="info-text-input" v-else v-model="mobileEditor.content"></textarea>
          </div>
          <div class="info-edit">
            <img v-if="!mobileEditor.visible" @click="showMobileEditor" src="../images/edit.png" />
            <img v-else @click="saveMobileNumber" src="../images/finish.png" />
          </div>
        </div>
        <div class="button-box">
          <button class="card-button" @click="chat(selectedCustomer.openId)">发消息</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Contacts",
    data() {
      return {
        customers: [],
        selectedCustomer: null,
        search: "",
        mobileEditor: {
          visible: false,
          content: ''
        },
        markEditor: {
          visible: false,
          content: ''
        }
      };
    },
    mounted() {
      this.queryContacts();
    },
    methods: {
      showMobileEditor () {
        this.mobileEditor.content = this.selectedCustomer.mobileNumber;
        this.mobileEditor.visible = true;
      },
      saveMobileNumber () {
        let params = {
          openId: this.selectedCustomer.openId,
          mobileNumber: this.mobileEditor.content
        }
        this.global.api.updateMobileNumber(params).then((res) => {
          this.mobileEditor.visible = false;
          this.selectedCustomer.mobileNumber = this.mobileEditor.content;
        }).catch((err) => {
          console.log("save or update mobile number failed:", err);
        });
      },
      showMarkEditor () {
        this.markEditor.content = this.selectedCustomer.mark;
        this.markEditor.visible = true;
      },
      saveMark () {
        this.global.api.updateUserMark(this.selectedCustomer.openId, this.markEditor.content).then((res) => {
          this.markEditor.visible = false;
          this.selectedCustomer.mark = this.markEditor.content;
        }).catch((err) => {
          console.log("save or update mark failed:", err);
        });
      },
      queryContacts() {
        this.global.api.getContacts(this.search).then((res) => {
          this.customers = res.data.list;
        }).catch((err) => {
          console.log();
        });
      },
      showCustomer(customer) {
        this.selectedCustomer = customer;
        this.mobileEditor = {
          visible: false,
          content: ''
        },
        this.markEditor = {
          visible: false,
          content: ''
        }
      },
      chat(customerId) {
        this.$router.push({
          path: '/agent/conversations/chat',
          query: {
            id: customerId,
          }
        });
      },
    },
  };
</script>

